// @spec: specs/002-fullstack-web-app/plan.md
// @spec: specs/002-fullstack-web-app/spec.md
// API client with JWT attachment

import { getToken } from "./auth";
import type {
  Task,
  TaskCreate,
  TaskUpdate,
  Tag,
  TagWithCount,
  TagCreate,
  TagUpdate,
  TaskListParams,
} from "@/types";

const API_URL = process.env.NEXT_PUBLIC_API_URL || "http://localhost:8000";

/**
 * API client for Todo application
 */
export const apiClient = {
  /**
   * Fetch tasks for a user
   */
  async getTasks(userId: string, params?: TaskListParams): Promise<Task[]> {
    const token = await getToken();
    const queryParams = new URLSearchParams();

    if (params?.search) queryParams.append("search", params.search);
    if (params?.status) queryParams.append("status", params.status);
    if (params?.priority) queryParams.append("priority", params.priority);
    if (params?.sort) queryParams.append("sort", params.sort);
    if (params?.order) queryParams.append("order", params.order);
    if (params?.tags) params.tags.forEach((tag) => queryParams.append("tag_ids", tag));

    const response = await fetch(
      `${API_URL}/api/${userId}/tasks?${queryParams.toString()}`,
      {
        headers: {
          Authorization: `Bearer ${token}`,
          "Content-Type": "application/json",
        },
      }
    );

    if (!response.ok) {
      throw new Error(`Failed to fetch tasks: ${response.statusText}`);
    }

    return response.json();
  },

  /**
   * Create a new task
   */
  async createTask(userId: string, task: TaskCreate): Promise<Task> {
    const token = await getToken();
    const response = await fetch(`${API_URL}/api/${userId}/tasks`, {
      method: "POST",
      headers: {
        Authorization: `Bearer ${token}`,
        "Content-Type": "application/json",
      },
      body: JSON.stringify(task),
    });

    if (!response.ok) {
      throw new Error(`Failed to create task: ${response.statusText}`);
    }

    return response.json();
  },

  /**
   * Update a task
   */
  async updateTask(
    userId: string,
    taskId: string,
    task: TaskUpdate
  ): Promise<Task> {
    const token = await getToken();
    const response = await fetch(`${API_URL}/api/${userId}/tasks/${taskId}`, {
      method: "PUT",
      headers: {
        Authorization: `Bearer ${token}`,
        "Content-Type": "application/json",
      },
      body: JSON.stringify(task),
    });

    if (!response.ok) {
      throw new Error(`Failed to update task: ${response.statusText}`);
    }

    return response.json();
  },

  /**
   * Patch a task (partial update, e.g., toggle complete)
   */
  async patchTask(
    userId: string,
    taskId: string,
    task: TaskUpdate
  ): Promise<Task> {
    const token = await getToken();
    const response = await fetch(`${API_URL}/api/${userId}/tasks/${taskId}`, {
      method: "PATCH",
      headers: {
        Authorization: `Bearer ${token}`,
        "Content-Type": "application/json",
      },
      body: JSON.stringify(task),
    });

    if (!response.ok) {
      throw new Error(`Failed to patch task: ${response.statusText}`);
    }

    return response.json();
  },

  /**
   * Delete a task
   */
  async deleteTask(userId: string, taskId: string): Promise<void> {
    const token = await getToken();
    const response = await fetch(`${API_URL}/api/${userId}/tasks/${taskId}`, {
      method: "DELETE",
      headers: {
        Authorization: `Bearer ${token}`,
        "Content-Type": "application/json",
      },
    });

    if (!response.ok) {
      throw new Error(`Failed to delete task: ${response.statusText}`);
    }
  },

  /**
   * Get tags for a user
   */
  async getTags(userId: string): Promise<TagWithCount[]> {
    const token = await getToken();
    const response = await fetch(`${API_URL}/api/${userId}/tags`, {
      headers: {
        Authorization: `Bearer ${token}`,
        "Content-Type": "application/json",
      },
    });

    if (!response.ok) {
      throw new Error(`Failed to fetch tags: ${response.statusText}`);
    }

    return response.json();
  },

  /**
   * Create a new tag
   */
  async createTag(userId: string, tag: TagCreate): Promise<Tag> {
    const token = await getToken();
    const response = await fetch(`${API_URL}/api/${userId}/tags`, {
      method: "POST",
      headers: {
        Authorization: `Bearer ${token}`,
        "Content-Type": "application/json",
      },
      body: JSON.stringify(tag),
    });

    if (!response.ok) {
      throw new Error(`Failed to create tag: ${response.statusText}`);
    }

    return response.json();
  },

  /**
   * Update a tag
   */
  async updateTag(
    userId: string,
    tagId: string,
    tag: TagUpdate
  ): Promise<Tag> {
    const token = await getToken();
    const response = await fetch(`${API_URL}/api/${userId}/tags/${tagId}`, {
      method: "PUT",
      headers: {
        Authorization: `Bearer ${token}`,
        "Content-Type": "application/json",
      },
      body: JSON.stringify(tag),
    });

    if (!response.ok) {
      throw new Error(`Failed to update tag: ${response.statusText}`);
    }

    return response.json();
  },

  /**
   * Delete a tag
   */
  async deleteTag(userId: string, tagId: string): Promise<void> {
    const token = await getToken();
    const response = await fetch(`${API_URL}/api/${userId}/tags/${tagId}`, {
      method: "DELETE",
      headers: {
        Authorization: `Bearer ${token}`,
        "Content-Type": "application/json",
      },
    });

    if (!response.ok) {
      throw new Error(`Failed to delete tag: ${response.statusText}`);
    }
  },
};
